<template>
    <div class="row no-gutters section">
        <template v-if="sceenWidth >= 1200">
            <img class="img-fluid img-rwd " src="../../../assets/images/page-home/section-top/20210115-DSCF9313-min.png" />
            <!-- <img class="img-fluid  img-rwd" src="../../../assets/images/page-home/section-top/20210115-DSCF9313-min-gray-min.png" /> -->
            <p style="z-index:1000;font-size:15vw;position:absolute; margin-top:10vw;left:10%">New</p>
        </template>
        <template v-if="sceenWidth >= 576 && sceenWidth < 1200">
            <img class="img-fluid  img-rwd" src="../../../assets/images/page-home/section-top/20210115-DSCF9313-2-min.png" />
            <p style="z-index:1000;font-size:15vw;position:absolute; margin-top:80vw;left:5%">New</p>
        </template>
        <template v-if="sceenWidth < 576">
            <img class="img-fluid img-rwd" src="../../../assets/images/page-home/section-top/20210115-DSCF9313-2-min.png" />
            <p style="z-index:1000;font-size:15vw;position:absolute; margin-top:130vw;left:5%">New</p>
        </template>
    </div>
</template>

<script>
import { gsap } from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";
export default {
    data() {
        return {
            sceenWidth: screen.width,
            sceenHeight: screen.height,
        };
    },
    mounted() {
        console.log(this.sceenWidth);
        let maskX = 0;
        let maskY = -70;
        let s = maskX + "px " + maskY + "px";
        console.log(s);
        gsap.registerPlugin(ScrollToPlugin, ScrollTrigger);
        var tl = gsap.timeline(); //建立動畫時間線
        tl.set(".img-rwd-color", { maskSize: "110vw 100vh", maskPosition: s });
        tl.to(".img-rwd-color", { duration: 0.5, delay: 1, clipPath: "polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%)" });
        tl.to(".img-rwd-color", { duration: 8, ease: "none", maskSize: "10000vw 10000vh", maskPosition: "-5000vw -5000vh" });
    },
};
</script>

<style scoped>
.section {
    height: 100vh;
}

.img-rwd {
    position: absolute;
    object-fit: cover;
    height: 100%;
    width: 100%;
}

.img-rwd-color {
    z-index: 1000;
    mask: url("../../../assets/images/page-home/section-top/mask-image.svg");
    -webkit-mask: url("../../../assets/images/page-home/section-top/mask-image.svg");

    clip-path: polygon(0% 0%, 20% 0%, -10% 100%, 0% 100%);
    mask-repeat: no-repeat;
    -webkit-mask-repeat: no-repeat;
    mask-size: clamp(1200px, 100%, 100vw) 100%;
    -webkit-mask-size: clamp(1200px, 100%, 100vw) 100%;
    /* mask-position: calc((100vw - 1920px) * 0.11) clamp(-3vh, -4vh, -5vh);
    -webkit-mask-position: calc((100vw - 1920px) * 0.11) clamp(-3vh, -4vh, -5vh); */
}
</style>
